.Card{
    flex:1 ;
    margin: 20px;
    border: 3px solid #000;
    border-radius: 10px;
    overflow: hidden;
    background-color: #fff;

    display: flex;
    flex-direction: column;
    min-width: 300px;
}

.Card .Content{
    flex: 1;
    padding: 15px 0px;
    background-color: #fff;
    color: #000;
}

.Card .Footer{
    background-color: #000;
    color: #fff;
    padding: 10px 0px;
}